import ListGroup from 'react-bootstrap/ListGroup';
import {Link} from 'react-router-dom';
import data from '../Data/orderdata';
import React, { Component } from 'react';
import '../css/Json_data1.css';
import bgi11 from '../images/background2.jpg'

const order_information_list = data.Order_Information;

class Json_data1 extends Component {

 state={
    test : [],
    lcs : ''
    }
componentDidMount()   //page load - > 
{
    let test =[];
    order_information_list.map((Order_Information) =>{
        test.push(Order_Information)     
        console.log(Order_Information);
    })
    console.log(test);
    this.setState( {test});
}
change_select = (event) =>{

    console.log(event.target.value);
    this.setState({lcs:event.target.value});  // this.setState('test'||{value: event.target.value});
    console.log(this.state.lcs);
    {this.renderjsondata()}
}

renderjsondata()
{
    let test1=this.state.test.length;
    let i =0;
    let j=0;
    let lcs = this.state.lcs;
    console.log('json aray size : '+lcs);
    
    let test2=[];
    console.log('lcs value before filter array:: ' +lcs);
  
    for(let num=i; num < test1; num++)
    {             
        let customer_status=this.state.test[num].cutomer_status;
        console.log(customer_status+"   "+lcs);
        if(lcs == customer_status || lcs=="All" || lcs == ""){
            test2.push(             
                <tr>
                   <td> {this.state.test[num].order_number}    </td>
                   <td> {this.state.test[num].customer}        </td>
                   <td> {this.state.test[num].cutomer_status}  </td>
                </tr>
        );      
            }          
    }
    return test2;
}
    render() {
       
      
      return (
          <div>
                 <img src={bgi11} id="bg"/> 
                   <div class="searhc_criteria">
                   <p>status</p>
                   <select name="customerstatus" onChange={this.change_select} value={this.state.lcs}> 
                                <option value="All">All</option>
                                <option value="Sold">Sold</option>
                                <option value="Active">Active</option>
                                <option value="Terminated">Terminated</option>
                                <option value="hardnostart">Hardnostart</option>
                    </select>
                   </div>             
                        <div class="customerpage">
                        <table border="1" position="center">
                            <tbody>
                            <tr>
                                    <th>order number</th>
                                    <th>customer</th>
                                    <th>Customer status</th>
                            </tr>
                            {this.renderjsondata()}
                            </tbody>
                        </table>     
                        </div>
         </div>
      );
        }
    
  }
  
  export default Json_data1;



