import React, {Component } from 'react';
import ChildComponent1 from './ChildComponent1'
import bgimpa1 from '../images/all_parentcomponent1.jpg'
import bgimch1 from '../images/child_parentcomponent1.jpg'

class ParentComponent1 extends Component {    
    constructor(props){
        super(props)
        this.state = {parentName :'Parent'}
        this.greetParent = this.greetParent.bind(this);
      }
    greetParent(childName) {
        //alert('Hello' + this.state.parentName);
        alert(`Hello ${this.state.parentName} from ${childName}`);  }
    render() {
        return (
            <div>
                <ChildComponent1 greetHandler={this.greetParent}/>
                <p>The above button is rendered from the another component(child component). Click on the button to get the properties associated to that method and look at the chldcomponent code to see how we are passing the method as the parameter to parent component from child component</p>
                <img src={bgimpa1}/>
                <img src={bgimch1}/>
            </div>)} } 
 export default ParentComponent1; 