import React, {Component } from 'react';
import ChildComponent from './ChildComponent'
import bgimpa from '../images/all_parentcomponent.jpg'
import bgimch from '../images/child_parentcomponent.jpg'

class ParentComponent extends Component {    
    constructor(props){
        super(props)
        this.state = {parentName :'Parent'}
        this.greetParent = this.greetParent.bind(this); }
    greetParent() {
        //alert('Hello' + this.state.parentName);
        alert(`Hello ${this.state.parentName}`);  }
    render() {
        return (
            <div>
                <ChildComponent greetHandler={this.greetParent}/>
                <p>The above button is rendered from the another component(child component). Click on the button to get the properties of the method associated to that method and look at the chldcomponent code to see how we are passing the eventHandler to parent class</p>
                <img src={bgimpa}/>
                <img src={bgimch}/>
            </div>)}} 
 export default ParentComponent;