import ParentComponent from './Alltopiccomponents/Parentcomponent'

class App extends Component {
  constructor(props) {
  super(props); 
  this.state ={
      component1_variable : " i belongs to component 1 , component 1 is my home",
      component2_variable : " i belongs to component 2 , component 2 is my home",
    }
  }

 click_comp1 = () => { 
  this.setState({component2_variable: "test"});
};

  render() {
    return (
      <div>
          {/* <img src={bgi1} id="bg"/> */}         
         <Router>
            <switch>
                <Route path="/" exact component={Home} ></Route>
                <Route path="/component_1" component={Component_1}  component2_variable ={this.component2_variable}></Route>
                <Route path="/component_2" component={Component_2}></Route>
                <Route path="/Bootstrap_react" component={Bootstrap_react}></Route>
                <Route path="/Redux_Info" component={Redux_Info}></Route>
                <Route path="/Material_UI" component={Material_UI}></Route>
                <Route path="/Temp_Dashboard" component={Temp_Dashboard}></Route>
                <Route path="/Stylebinding" component={Stylebinding}></Route>
                <Route path="/Json_data1" component={Json_data1}></Route>
                <Route path="/Stateinforeact" component={Stateinforeact}></Route>
                <Route path="/Sample1dash" component={Sample1dash}></Route>
                <Route path="/Alltopicsdetails" component={Alltopicsdetails}></Route>                
                <Route path="/Bindingeventhand" component={Bindingeventhand}></Route>            
                <Route path="/Methodsasprops" component={Methodsasprops}></Route>
            </switch>          
         </Router>    
      </div>
    );
  }
}

