import React, { Component } from 'react';
import Bootstrap_react from '../headertab_comptns/bootstrap_react';
import {Link} from 'react-router-dom';
import Redux_Info from '../headertab_comptns/Redux_Info';

class Headertabs extends Component {
  render() {
    return (
        <div class="headertab_div">
          <Link to="/Bootstrap_react">
                Bootstrap-react (Headertabs)
            </Link> 
            <Link to="/Redux_Info">
                Redux
            </Link>
            <Link to="/Material_UI">
                Material-UI
            </Link>
            <Link to="/Material_UI">
                DashBorad-1
            </Link>
        </div>
    );
  }
}

export default Headertabs;






















