import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import '../css/Stylebinding.css';


// const changestyle_bing = {
//   color:'white'
// }


class Stylebinding extends Component {
   state = {
        changestyle_bing : { color: 'red' , border:'solid'},
        div_style_change_variable : { border:'' }
   }
   changeStle = () => {
    console.log("i am here");
    this.setState({changestyle_bing:{color:'white'}})
    this.setState({changestyle_bing:{border:''}})
    console.log("i am here. after assigning the properties");
  }

   render() {
    return (       
         <div class="stylebindinga" style = {this.state.changestyle_bing}>
             <form>
               <input type="text" value="text123" align="center"></input>
               <p>** hi binding is not applied for me,can you set binding for me</p>    
               
             </form>     
             <button onClick={this.changeStle} value="changestyle">changebutton</button>              
        </div>
    );
  }
}
export default Stylebinding;





