import React, { Component } from 'react';
import {Link} from 'react-router-dom';

class Stateinforeact extends Component {

    constructor(props) {
        super(props); 
    }

display_com1_pros = () => {
      this.props.click_comp1();
  };
  
  render() {
    return (
        <div>
             <div>
                <Link to="/Component_1" onClick={this.display_com1_pros}>
                    Component1
                </Link><br></br>
                <Link to="/Component_2" onClick={this.display_com2_pros}>
                    Component2
                </Link>
             </div>
             
        </div>
    );
  }
}

export default Stateinforeact;

